#wx-charts {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  //width: 92vw;
  width: 99%;
}

.customized-legend-wxgraph {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  //margin-top: 0.5vw;
  //margin-bottom: 0.5vw;
  width: 99%;
  //height: calc(20px + 1.0vw);
  //background-color: #fff;
  //font-size: calc(10px + 0.8vw);
  font-family: Roboto;
  //line-height: calc(1em + 0.5vw);
  text-align: center;
}

.legent-item-wxgraph {
  margin: 1vw;
}

.customized-legend {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  //padding: 20px;
  margin-top: 0.5vw;
  margin-bottom: 0.5vw;
  //margin-left: 10vw;
  width: 94vw;
  height: calc(20px + 1.0vw);
  //height: 30px;
  background-color: #fff;
  font-size: calc(10px + 0.8vw);
  line-height: calc(1em + 0.5vw);
  text-align: center;
}

.customized-tooltip-wxgraph {
  //margin-top: 6vw;
  //font-size: calc(6px + 0.7vw);
  font-family: Roboto;
  line-height: calc(1em + 0.5vw);
  background-color: #ffffff;
  padding: 6px;
  border-color: #000000;
  border: 1px solid;
}

.tooltip-item {
//  margin: 2vw;
}

.legend-item {
  margin: 1vw;
}

.legend-symbol {
  margin: 0 10 0 0;
}

//.recharts-cartesian-axis { font-size: calc(8px+1.0vw); font-family: sans-serif; }
.recharts-cartesian-axis { font-size: calc(10px + 0.2vw); font-family: sans-serif; }

.isHidden {
